.problem_detail-flex-container {
    display: flex;
    width: 100%;
    max-width: 100%;
    justify-content: space-around;
    align-items: flex-start;
    flex-flow: row nowrap;
    background-color: #eee;
}

.problem-main {
    width: 80%;
}

.problem-main__item {
    margin: 35px 0;
}

.dropdown-languege {
    display: flex;
}
.problem-id {
    display: flex;
}
.submit-nav {
    display: flex;
    justify-content: space-between;
    label {
        padding-right: 10px;
    }
    .submit-nav__item {
        padding: 0 10px;
    }
}
#submit-button {
    background-color: #1c2237;
}
#submit-button:hover {
    background-color: rgba(35, 38, 54, 0.815);
    box-shadow: none;
}
.right-column {
    flex: none;
    margin: 35px 0;
}

.submit-card {
    text-align: center;
    align-items: center;
    width: 100%;
}

.right-column p {
    margin-left: 10px;
    margin-right: 10px;
}
.to_statusPage_card {
    display: flex;
    justify-content: flex-start;
    font-weight: 600;
    .to_statusPage_card-item {
        width: 20px;
        margin-top: 5px;
    }
    height: 20px;
}
.to_statusPage_card:hover {
    text-decoration: none;
}
.right-column__item {
    margin-bottom: 60px;
    width: 15rem;
}

.problem-information__item {
    display: flex;
    justify-content: space-between;
    text-align: center;
    height: 3rem;
}
.problem-information_header {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    height: 60px;
}

.title,
.time_limit,
.memory_limit {
    text-align: center;
}

.description,
.input_description,
.output_description {
    word-wrap: break-word;
}
.problem-main__footer {
    display: flex;
    justify-content: flex-end;
}

textarea {
    background-color: #fff;
    resize: none;
}
.form-control:disabled,
.form-control[readonly] {
    background-color: #fff;
}
.input-header {
    position: relative;
    justify-content: space-between;
    padding-right: 15px;
    margin-bottom: 3px;
    .copy-btn {
        position: absolute;
        top: 10px;
        right: 10px;
        display: block;
        padding: 0 10px;
        font-size: 15px;
        color: #767676;
        cursor: pointer;
        background-color: #fff;
        border: 1px solid #ccc;
        border-radius: 0 10px 0 10px;
    }
    .copy-btn:hover {
        color: #fff;
        background-color: #563d7c;
        border-color: #563d7c;
    }
}
.pd__sample {
    display: flex;
}

.problem-information {
    transform: translateY(-56px);
}
